.sect1__otadiv {
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(2,1fr);
}

.sect1__otadiv__card {
    text-align: end;
    display: grid;
    grid-template-columns: repeat(2,1fr);
}


.sect1_otadiv__card__text__btn {
    width: 140px;
    height: 40px;
    border: none;
    color: aliceblue;
    background: #46A346;
    border-radius: 6px;
}



@media all and (max-width: 769px) {
    .sect1__otadiv__card {
    text-align: end;
    display: grid;
    grid-template-columns: repeat(1,1fr);
}
}

@media all and (max-width: 426px) {
    .sect1__otadiv {
        display: grid;
        gap: 20px;
        row-gap: 20px;
        grid-template-columns: repeat(1,1fr);
    }
}
