.section__otadiv {
    padding: 25px;
}

.otadiv__card__img {
    width: 500px;
    height: 500px;
}

.section__otadiv__card {
    display: grid;
    grid-template-columns: repeat(2,1fr);
}

.section__otadiv__card__text__div {
    display: flex;
    justify-content: space-between;
}

.section__otadiv__card__text__div__btn {
    display: flex;
    gap: 20px;
}

.section__otadiv__card__text__div__btn1 {
    border: 1px solid #EAEAEA;
    background: #ffffff;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    padding: 5px;
    font-size: 15px;
    color: #727272;
}

.active1 {
    width: 40px;
    height: 40px;
    border: 1px solid #46A356;
    color: #46A356;
    background: #ffffff;
    border-radius: 50%;
}

.section__otadiv__card__text__div__button {
    display: flex;
}

.section__otadiv__card__text__div__button1 {
    width: 33px;
    height: 38px;
    border-radius: 31px;
    color: #ffffff;
    border: none;
    background: #46A356;
}

.ifel__btn {
    width: 130px;
    height: 40px;
    border: 1px solid #46A356;
    border-radius: 6px;
    background: #ffffff;
    color: #46A356;
    transition: 0.3s;
}

.ifel__btn1 {
    width: 142px;
    height: 40px;
    border: 1px solid #46A356;
    border-radius: 6px;
    background: #ffffff; 
    transition: 0.3s;
}

@media all and (max-width: 769px) {
    .section__otadiv__card {
        display: grid;
        grid-template-columns: repeat(1,1fr);
    }
    .otadiv__card__img {
        width: 600px;
        height: 600px;
    }
}

@media all and (max-width: 476px){
    .otadiv__card__img {
        width: 100%;
        height: 300px;
    }
}