.sect4 .container  {
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(2,1fr);
}

.sect4__otadiv__card__div__img {
    display: flex;
    gap: 1px;
}

.sect4__otadiv__card__div__img1 {
    width: 90px;
    height: 88px;
}

.sect4__otadiv__card__div__ifels__btn, .sect4__otadiv__card__div__ifels__btn1 {
    color: aliceblue;
    background: #46A356;
    border: none;
    border-radius: 30px;
    width: 30px;
    height: 32px;
}

.sect4__otadiv__card__div {
    padding: 10px;
    border-radius: 1pc;
    background: #FBFBFB;
    display: grid;
    grid-template-columns: repeat(4,1fr);
}

.sect4__otadiv__card__div__img__text {
    padding: 10px;
}

.sect4__otadiv__card__div__h5, .sect4__otadiv__card__div__ifels {
    padding-top: 24px;
}

.sect4__otadiv__card__div__btn {
    margin-left: 55px;
    width: 120px;
    height: 40px;
    border: none;
    background: #46A356;
    border-radius: 7px;
    color: aliceblue;
    margin-top: 21px;
}

.sect4__otadiv__card__div__img__text2 {
    width: 200px;
}

.sect4__otadiv__card__div__ifels__son {
    padding-top: 2px;
}


.sect4__otadiv1__card1 {
    display: flex;
    justify-content: space-between;
}

.sect4__otadiv__card__div__ifels {
    padding-left: 55px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
}

.sect4__btn1 {
    width: 100%;
    height: 50px;
    border: none;
    color: aliceblue;
    background: #46A356;
    border-radius: 6px;
}

.sect4__btn__h6 {
    color: #46A356;
    padding-top: 10px;
    text-align: center;
}

.sect4__otadiv1__card, .sect4__otadiv1__text {
    color: #3D3D3D;
}

@media all and (max-width: 1024px) {
    .sect4__otadiv__card__div__ifels {
        padding-left: 25px;
        display: grid;
        grid-template-columns: repeat(3,1fr);
    }
    .sect4__otadiv__card__div__btn {
        margin-left: 25px;
        width: 120px;
        height: 40px;
        border: none;
        background: #46A356;
        border-radius: 7px;
        color: aliceblue;
        margin-top: 21px;
    }
    /* .sect4__grid__h5 {
        padding-top: 5px;
    } */
    .sect4__grid__h51 {
        padding-top: 24px;
    }
}

@media all and (max-width: 769px) {
    .sect4 .container  {
        display: grid;
        gap: 20px;
        grid-template-columns: repeat(1,1fr);
    }
    .sect4__otadiv__card__div__ifels {
        padding-left: 20px;
        display: grid;
        grid-template-columns: repeat(3,1fr);
    }
    .sect4__otadiv__card__div__btn {
        margin-left: 20px;
        width: 120px;
        height: 40px;
        border: none;
        background: #46A356;
        border-radius: 7px;
        color: aliceblue;
        margin-top: 21px;
    }
    .sect4__otadiv__card__div__img__text2 {
        width: 150px;
    }   
}


@media all and (max-width: 426px) {
    .sect4__otadiv__card__div {
        padding: 10px;
        border-radius: 1pc;
        background: #FBFBFB;
        display: grid;
        grid-template-columns: repeat(2,1fr);
    }
    .sect4__otadiv__card__div__ifels {
        display: flex;
        gap: 5px;
    }
    .sect4__otadiv__card__div__btn {
        width: 120px;
        height: 40px;
        border: none;
        background: #46A356;
        border-radius: 7px;
        color: aliceblue;
        margin-top: 21px;
    }
    .sect4__otadiv__card__div__img__text2 {
        font-size: 17px;
        width: 100px;
    }   
}