section {
    padding-top: 30px;
}

.text1 {
    color: #46A358;
}

.text1__h6 {
    color: #3D3D3D;
}


.sect__card1__card__img {
    width: 250px;
    height: 260px;
}

.sect__otadiv {
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(2,1fr);
}

.sect__card1 {
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 20px;
    row-gap: 20px;
}



.textla {
    background: #FBFBFB;
    width: 310px;
    padding-left: 1px;
}

.text1 , .text2 {
    padding: 12px;
    display: flex;
    justify-content: space-between;
}

.text__input {
    padding: 15px;
}

.text__input__btn {
    width: 95px;
    height: 35px;
    background: #46A358;
    color: aliceblue;
    border: none;
    border-radius: 6px;
}

.text__img {
    width: 300px;
}

.sect__card1__text {
    display: flex;
    justify-content: space-between;
}


.sect__card1__text__h6 {
    display: flex;
    gap: 30px;
}

.sect__card1__text__h6__active {
    color: #46A358;
    text-decoration: underline;
}

.sect__card1__text__h6__hr {
    color: #3D3D3D;
}

.slider__btn__activ {
    width: 35px;
    height: 35px;
    border: none;
    color: aliceblue;
    border-radius: 4px;
    background: #46A358;
}


.slider {
    display: flex;
    justify-content: flex-end;
    padding: 20px;
    gap: 10px;
}

.slider__btn {
    width: 35px;
    height: 35px;
    border: 2px solid #f5f5f5;
    color: #3D3D3D;
    border-radius: 4px;
    background: #ffffff;
}




@media all and (max-width: 1025px) {
    .sect__card1 {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 20px;
    row-gap: 20px;
}
}




@media all and (max-width: 769px){
     .sect__card1 {
    display: grid;
    grid-template-columns: repeat(1,1fr);
    gap: 20px;
    row-gap: 20px;
}
.sect__otadiv {
    display: grid;
    gap: 50px;
    grid-template-columns: repeat(2,1fr);
}
}


@media all and (max-width: 426px) {
    .sect__otadiv {
        display: grid;
        gap: 10px;
        grid-template-columns: repeat(1,1fr);
    }.sect__card1__text__h6__hr {
        display: none;
    }
}