* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.ada__div {
    display: flex;
    justify-content: space-between;    
}

nav {
    padding-top: 15px;
}

.nav__ul1 {
    padding-top: 55px;
}


.nav__ul1 li {
    font-size: 18px;
}

a {
    color: black;
    text-decoration: none;
}

.activ {
    text-decoration: underline;
}

.nav__ul2 {
    display: flex;
    padding-top: 55px;
}

.nav__ul2__btn {
    background: #46A358;
    border: none;
    border-radius: 7px;
    width: 100px;
    height: 35px;
    color: aliceblue;
}

.nav__ul3 {
    display: flex;
    padding-right: 50px;
    gap: 30px;
    font-size: 19px;
}

.cartki {
    color: #3D3D3D;
    font-size: 20px;
    padding-right: 2px;
    padding-top: 5px;
}


.nav__btn {
    border: none;
    background: none;
    font-size: 17px;
}

.nav__ul {
    padding-top: 5px;
}

.nav__son {
    font-size: 10px;
}

@media all and (max-width: 426px) {
    .nav__grid {
        display: none;
    }
    .nav__grid__rasm {
        width: 110px;
    }
    .nav__ul3 {
        display: flex;
        padding-right: 5px;
        gap: 30px;
        font-size: 19px;
    }
}

@media all and (max-width: 376px) {
    .activ {
        display: none;
    }
     .nav__ul3 {
        display: flex;
        padding-right: 1px;
        gap: 30px;
        font-size: 19px;
    }

    .btn {
        padding: 1px;
    }
}