.header__otadiv {
    background: #f5f5f5;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 35px;
}

@media all and (max-width: 769px){
    .header__otadiv {
    background: #f5f5f5;
    display: grid;
    grid-template-columns: repeat(1,1fr);
    gap: 35px;
}
}

.otadiv__card {
    margin-top: 30px;
    padding: 25px;
}

.otadiv__card__h1 {
    font-size: 60px;
}

.otadiv__card__h6 {
    color: #727272;
}

.otadiv__card__h5 {
    color:   #3D3D3D;
}

.otadiv__card__btn {
    border: none;
    border-radius: 6px;
    color: aliceblue;
    background: #46A358;
    width: 150px;
    height: 40px;
}

@media all and (max-width: 426px) {
    .otadiv__card__h1 {
        font-size: 27px;
    }
    .otadiv__card {
        margin-top: 3px;
        padding: 2px;
    }   
    .hedr__grid__img {
        width: 100%;
    }
}

