footer {
    background: #FBFBFB;
    padding: 15px;
}


.footer__otadiv {
    display: grid;
    grid-template-columns: repeat(4,1fr);
}

footer ul li {
    color: #3D3D3D;
}

.footer__otadiv__i1 {
    font-size: 20px;
    color: #46A356;
    border: 1px solid rgb(180, 222, 177);
    border-radius: 5px;
    padding: 5px;
}

@media all and (max-width: 769px) {
    .footer__grid {
        display: none;
    }
}

@media all and (max-width: 426px) {
    .footer__otadiv {
        display: grid;
        grid-template-columns: repeat(2,1fr);
    }
    .footer__grid__img {
        width: 120%;
    }
}