.sect2__text {
    text-align: center;
}

.sect2__h6 {
    color: #727272;
}

.sect2__otadiv__card__img {
    width: 268px;
    height: 200px;
}
.sect2__otadiv__card__h6 {
    padding-top: 5px;
   color: #46A356;
}

.sect2__otadiv {
    display: grid;
    grid-template-columns: repeat(3,1fr);
}

.sect2__otadiv__card__text {
    padding: 10px;
}



@media all and (max-width: 1025px) {
    .sect2__otadiv {
        display: grid;
        gap: 20px;
        grid-template-columns: repeat(2,1fr);
    }
    .sect2__otadiv__card__img {
        width: 300px;
        height: 280px;
    }
}

@media all and (max-width: 426px) {
    .sect2__otadiv {
        display: grid;
        grid-template-columns: repeat(1,1fr);
    }
}