aside {
    padding-top: 20px;
}

aside .container {
    padding: 5px;
    background: #FBFBFB;
}

.aside__otadiv {
    display: grid;
    grid-template-columns: repeat(4,1fr);
}

@media all and (max-width: 769px) {
    .aside__otadiv {
        display: grid;
        row-gap: 20px;
        grid-template-columns: repeat(2,1fr);
    }
}

.aside__otadiv__inpt__h6 {
    color: #3D3D3D;
    font-size: 19px;
}

.aside__otadiv__inpt__p {
    color: #727272;
    font-size: 15px;
}

.aside__otadiv__inpt__btn {
    color: aliceblue;
    background: #46A356;
}







.sect3 .container {
    height: 10vh;
    background: #46A3581A;
}

.sect3__otadiv {
    display: grid;
    grid-template-columns: repeat(4,1fr);
    padding-top: 10px;
}

.sect3__otadiv__card {
    color: #727272;
    display: flex;
    gap: 10px;
}

.sect3__otadiv__card__i {
    padding-top: 5px;
    color: #46A356;
}

.sect3__otadiv__card__i1 {
    color: #46A356;
    padding-top: 15px;
}

.sect3__otadiv__img {
    padding-top: 13px;
}

.sect3__otadiv__card:nth-child(2) {
    padding-top: 5px;
}

.sect3__otadiv__card:nth-child(3) {
    padding-top: 13px;
}

.sect3__otadiv__card:nth-child(4) {
    padding-top: 13px;
}



@media all and (max-width: 1025px) {
    .sect3 .container {
        height: 17vh;
        background: #46A3581A;
    }
}


@media all and (max-width: 769px) {
    .sect3 .container {
        height: 30vh;
        background: #46A3581A;
    }
    .sect3__otadiv {
        display: grid;
        grid-template-columns: repeat(2,1fr);
        padding-top: 10px;
    }
}

@media all and (max-width: 426px) {
    .asiide__grid__img {
        width: 110%;
    }
    .sect3 .container {
        height: 55vh;
        background: #46A3581A;
    }
    .sect3__otadiv {
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap: 10px;
        padding-top: 10px;
    }
}



@media all and (max-width: 376px ) {
    .sect3__otadiv {
        display: grid;
        grid-template-columns: repeat(1,1fr);
        gap: 10px;
        padding-top: 10px;
    }
    .sect3 .container {
        height: 65vh;
        background: #46A3581A;
    }
}
